import React, { useState } from "react";
import echarts from "echarts";
import _ from "lodash";
import DATA from "./data";

let name = "day";
const { allData, anger, joy, fear, sadness, hours } = DATA;

let xAxis = [];
var emotionData = [];
let index = 0;
xAxis = _.take(hours, index);
emotionData = [].concat(
  _.take(anger, index),
  _.take(joy, index),
  _.take(fear, index),
  _.take(sadness, index)
);
let play = false;
let timer = setInterval(() => {
    play = true;
  index += 1;
  xAxis = _.take(hours, index);
  emotionData = [].concat(
    _.take(anger, index),
    _.take(joy, index),
    _.take(fear, index),
    _.take(sadness, index)
  );
  Init(xAxis, emotionData);
  if (index == hours.length) {
    play = false;
    index = 0;
    xAxis = _.take(hours, index);
    emotionData = [].concat(
    _.take(anger, index),
    _.take(joy, index),
    _.take(fear, index),
    _.take(sadness, index)
    );
    clearInterval(timer);
  }
}, 1000);

function Init(xAxis = [], emotionData = []) {
  // const { name } = props;
  const dom = document.getElementById(name);
  const colors = ["#FC5302", "#C92A29", "#2F06FC", "#FFDB16"];
  var emotions = ["Joy", "Anger", "Fear", "Sadness"];

  let option = {
    tooltip: {
      position: "top",
    },
    title: [],
    singleAxis: [],
    series: [],
    grid: {
      left: "20%",
      right: "20%",
    },
  };

  echarts.util.each(emotions, function (day, idx) {
    option.singleAxis.push({
      left: 50,
      type: "category",
      boundaryGap: false,
      data: xAxis,
      top: (idx * 100) / 4 + 5 + "%",
      height: 100 / 4 - 10 + "%",
      axisLine: {
        lineStyle: {
          color: "#fff",
        },
      },
      axisLabel: {
        interval: 2,
        color: "#fff",
      },
    });
    option.series.push({
      singleAxisIndex: idx,
      coordinateSystem: "singleAxis",
      type: "scatter",
      data: [],
      color: colors[idx],
      tooltip: {
        formatter: (a) => {
            return a.name + '-' + day + ":" + a.data[1]/10
        }
      },
      symbolSize: function (dataItem) {
        return dataItem[1] * 4;
      },
    });
  });

  echarts.util.each(emotionData, function (dataItem) {
    option.series[dataItem[0]].data.push([dataItem[1], dataItem[2]]);
  });

  const myChart = echarts.init(dom);
  if (option && typeof option === "object") {
    myChart.setOption(option, true);
  }
}

export default function LineChart(props) {
  name = props.id;
  setTimeout(() => {
    Init();
  }, 0);
  return <React.Fragment>
      {/* <button onClick={() => { Init() }} disabled={!play}>Click</button> */}
      <div id={name} style={{ width: "100%", height: "400px" }}></div>
  </React.Fragment>;
}
