import React from "react";
import _ from "lodash";

export default function HomePage(props) {
  setTimeout(() => {
    var canvas = document.querySelector("#scene"),
      ctx = canvas.getContext("2d"),
      particles = [],
      amount = 0,
      mouse = { x: 0, y: 0 },
      radius = 1;

    // var colors = ["#468966", "#FFF0A5", "#FFB03B", "#B64926", "#8E2800"];
    var colors = ["#FC5302", "#C92A29", "#2F06FC", "#FFDB16"];
    var value = [34, 40, 12, 14];

    var copy = {
      value: ("#" + props.name).toUpperCase(),
    };

    var ww = (canvas.width = 800);
    var wh = (canvas.height = 200);

    function Particle(x, y, color) {
      this.x = Math.random() * ww;
      this.y = Math.random() * wh;
      this.dest = {
        x: x,
        y: y,
      };
      // this.r = Math.random() * 5;
      this.r = 2;
      this.vx = (Math.random() - 0.5) * 20;
      this.vy = (Math.random() - 0.5) * 20;
      this.accX = 0;
      this.accY = 0;
      this.friction = 0.94;

    //   this.color = colors[Math.floor(Math.random() * 6)];
    this.color = color;

    }

    Particle.prototype.render = function () {
      this.accX = (this.dest.x - this.x) / 1000;
      this.accY = (this.dest.y - this.y) / 1000;
      this.vx += this.accX;
      this.vy += this.accY;
      this.vx *= this.friction;
      this.vy *= this.friction;

      this.x += this.vx;
      this.y += this.vy;

      ctx.fillStyle = this.color;
      ctx.beginPath();
      ctx.arc(this.x, this.y, this.r, Math.PI * 2, false);
      ctx.fill();

      var a = this.x - mouse.x;
      var b = this.y - mouse.y;

      var distance = Math.sqrt(a * a + b * b);
      if (distance < radius * 70) {
        this.accX = (this.x - mouse.x) / 100;
        this.accY = (this.y - mouse.y) / 100;
        this.vx += this.accX;
        this.vy += this.accY;
      }
    };

    function onMouseMove(e) {
      mouse.x = e.clientX;
      mouse.y = e.clientY;
    }

    function onTouchMove(e) {
      if (e.touches.length > 0) {
        mouse.x = e.touches[0].clientX;
        mouse.y = e.touches[0].clientY;
      }
    }

    function onTouchEnd(e) {
      mouse.x = -9999;
      mouse.y = -9999;
    }

    function initScene() {
      ww = canvas.width = 800;
      wh = canvas.height = 200;

      ctx.clearRect(0, 0, canvas.width, canvas.height);

      ctx.font = "bold " + ww / (copy.value.length > 12 ? 12 : 10) + "px sans-serif";
      ctx.textAlign = "center";
      ctx.fillText(copy.value, ww / 2, wh / 2);

      var data = ctx.getImageData(0, 0, ww, wh).data;
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.globalCompositeOperation = "screen";

      particles = [];
      for (let i = 0; i < ww; i += Math.round(ww / 150)) {
        for (let j = 0; j < wh; j += Math.round(ww / 150)) {
          if (data[(i + j * ww) * 4 + 3] > 150) {
            particles.push({
              i: i,
              j: j,
            });
          }
        }
      }
      amount = particles.length;
      let rest = amount;
      let ar = value.map((item, index) => {
        if (index !== value.length) {
          let len = Math.ceil((item / 100) * amount);
          rest = amount - len;
          return _.fill(Array(len), colors[index]);
        } else {
          return _.fill(Array(rest), colors[index]);
        }
      });
      let c = [].concat.apply([], ar);
      for (var k = 0; k < amount; k++) {
        particles[k] = new Particle(particles[k].i, particles[k].j, c[k]);
      }
    }

    function onMouseClick() {
      radius++;
      if (radius === 5) {
        radius = 0;
      }
    }

    function render(a) {
      requestAnimationFrame(render);
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      for (var i = 0; i < amount; i++) {
        particles[i].render();
      }
    }

    // copy.addEventListener("keyup", initScene);
    window.addEventListener("resize", initScene);
    // window.addEventListener("mousemove", onMouseMove);
    // window.addEventListener("touchmove", onTouchMove);
    // window.addEventListener("click", onMouseClick);
    // window.addEventListener("touchend", onTouchEnd);
    initScene();
    requestAnimationFrame(render);
  }, 0);

  return <canvas id="scene"></canvas>;
}
